*{
    margin: 0;
    box-sizing: border-box;
    padding: 0;
}

.containerHome{
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 100vh;
    flex-direction: column;
    background-color: rgb(29, 39, 48);
    


}

.containerForm{
    display: flex ;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;
    height: 70%;
    width: 80%;
    border-radius: 1rem;
    box-shadow: 0 4px 10px rgba(23, 23, 23, 0.719);
    padding: 2rem;
    background-color:  rgba(48, 60, 71, 0.502);
    background-repeat: no-repeat;
    background-size: cover;
    text-align: center;
}

.buttonForm{
    width: min-content;
}

.selecForm{
    border-color: rgba(182, 179, 179, 0.637);
    height: 2rem;
    width: min-content;
    outline: none;
    background-color: transparent;
    color: white;
    
}

input{
    outline: none;
    border-style: none;
    width: 100%;
    text-align: center;
    background-color: transparent;
    border-style: solid;
    border-width:1.5px;
    color: white;
    appearance: none;
    
}

input:hover{
    border-color: coral;
    box-shadow: 0px 0px 3px tomato;
}

.selecForm:hover{
    border-color: coral;
    box-shadow: 0px 0px 3px tomato;
}

option{
    background-color: black;
}